
import { defineComponent } from "vue";
import "animate.css";
export default defineComponent({
  el: "#app",
  data() {
    return {
      isActive: false,
      windowWidth: window.innerWidth,
    };
  },
  methods: {
    toggleMobileNav: function () {
      this.isActive = !this.isActive;
      console.log(this.isActive);
    },
